html {
  /* overflow-x: hidden ;  */
  max-width: 100vw;
}
body {
  /* background-color: rgb(243, 243, 243); */
  /* background: linear-gradient(to left top, #F3EADA, #E8FEF9); */
  background-color: #fdf9f1;
  width: 100vw;
  height: 100%;
  margin: 0px;

  /* padding: 0px; */
  overflow-x: hidden !important;
  font-family: sans-serif;
  color: #284243;
  /* background-color: 284243; */
  box-sizing: border-box;
  line-height: 1.4rem;
}

.sticky {
  position: sticky;
  top: 0px;
}

.width100 {
  width: 100vw;
}

.width90 {
  width: 90vw;
}
.width80 {
