}
.max2000 {
  max-width: 2000px !important;
}

.padding1 {
  padding: 1%;
}
.padding2 {
  padding: 2%;
}

.padding3 {
  padding: 3%;
}
h1 {
  /* font-weight: 700 !important; */
}

.margin {
  margin: auto !important;
  right: 0;
  left: 0;
}

.margin2 {
  margin: 2%;
}
.marginleft2 {
  margin-left: 2ch;
}

.justify {
  justify-content: center !important;
}

.justifystart {
  justify-content: flex-start !important;
}

.justifyend {
  justify-content: flex-end;
}
.justifyaround {
  justify-content: space-around !important;
}

.justifybetween {
  justify-content: space-between !important;
}

.aligncontent {
  align-content: center !important;
}

.alignitems {
  align-items: center !important;
}

.textalign {
  text-align: center !important;
}

.flex {
  display: flex;
  flex-wrap: nowrap;
}

.flexwrap {
  display: flex;
  flex-wrap: wrap;
}

.flexcol {
  display: flex;
  flex-direction: column;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.absolutetop {
  position: absolute;
  top: 0%;
  right: 0;
  left: 0;
}

.absolutebottom {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}
.marginright1 {
  margin-right: 1%;
}
.marginright2 {
  margin-right: 2%;
}
.marginright5 {
  margin-right: 5%;
}
.margintop {
  margin-top: 5% !important;
}

.margintop10 {
  margin-top: 10% !important;
}

.margintop15 {
  margin-top: 15% !important;
}

.margintop25 {
  margin-top: 25% !important;
}

.margintop50 {
  margin-top: 50% !important;
  overflow: hidden !important;
}

.overflow {
  overflow: hidden !important;
}

hr {
  width: 80vw;
  background-color: #01a6e7;
}

.hrblue {
  background-color: 2px solid #01a6e7;
  width: 1px;
  height: 500px;
  display: inline-block;
  position: absolute;
  margin: 10%;
  opacity: 0.7;
}
.hrsticky {
  /* position:sticky; */
  top: 15%;
  /* bottom:25%; */
}

.head {
  width: 100vw;
}

