.hide {
}

.font500 {
  font-weight: 500;
}

.font600 {
  font-weight: 600;
  font-size: 0.8rem;
}

.font700 {
  font-weight: 700;
}

@media (max-width: 950px) {
  .hide {
    display: none;
  }
}

@media (max-width: 950px) {
  .fontsmall {
    font-size: 70%;
    width: 100vw;
    height: 50%;
  }
}

* {
  margin: 0px;

  overflow-x: clip !important;
}

@media (max-width: 668px) {
  body {
    font-size: 0.8em;
  }
}
